export default class ConfirmationYN {
  createElement(name, type) { // eslint-disable-line
    const element = document.createElement(`${name}`);
    element.classList.add(type);
    element.dataset.type = type;
    return element;
  }

  ask(parentContainer, questionText = 'Are you sure?') {
    const answer = { value: null };
    const buttons = [this.createElement('button', 'yes'), this.createElement('button', 'no')];
    buttons.forEach((b) => b.addEventListener('click', ({ target, currentTarget }) => {
      if (currentTarget === target) {
        answer.value = (target.dataset.type === 'yes');
      }
    }));
    this.container = this.createElement('div', 'confirmationYN');
    const text = document.createElement('p');
    text.textContent = questionText;
    this.container.append(text, ...buttons);
    parentContainer.appendChild(this.container);
    buttons[0].focus();
    return answer;
  }

  remove() {
    this.container.remove();
  }
}
